.backdrop {
  width: 100vw;
  height: 100vh;
  background: #0000004d;
  justify-content: center;
  align-items: center;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .3s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  pointer-events: none;
  visibility: hidden;
  opacity: 0;
}

.is-hidden .modal {
  transform: translate(-50%, -50%) scale 0 rotate(45 deg);
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(0)rotate(-180deg);
}

